import React from 'react';
import loadingScreen from '../../../utilities/loadingScreen';
import axios from 'axios';
class BlogDetail extends React.Component {


	componentDidMount() {
		loadingScreen.hideLoading();
	}

	componentWillMount() {
		loadingScreen.showLoading();
		axios.get('/datatest/Blog.json').then((res) => {
			console.log(this.props.match.params.blog_id);

			console.log(res);
			this.setState({
				product: res,
			}, loadingScreen.hideLoading())
		}).catch((err) => {
			loadingScreen.hideLoading();
			console.error(err);
		})
	}

	render() {

		let styleCenter;
		styleCenter = {
			margin: '40px auto 0',
			width: '70%',
		}
		return (
			<>
				<div id="breadcrumb">
					<div className="container">
						<div className="row">
							<ul className="breadcrumb">
								<h2 className="page-title" style={{ fontFamily: 'Times New Roman' }}>Blog</h2>
								<li><a href="/"><i className="fa fa-home"></i></a></li>
								<li><a href="/" style={{ pointerEvents: 'none', cursor: "default" }}>Blog</a></li>
							</ul>
						</div>
					</div>
				</div>
				<div className="container">
					<div className="row">
						<div id="content" className="single-blog" style={styleCenter}>
							<h2 className="page-title"> NGHA HOA HNG</h2>
							<div className="blog-img">
								<img src="http://splashythemes.com/opencart/OPC01/OPC010011/OPC3/image/cache/catalog/demo/blogs/6-835x455.jpg" alt="Nullam ullamcorper nisl quis" title="Nullam ullamcorper nisl quis" className="img-thumbnail" />
							</div>
							<p>Mi mu sc hoa hng li mang n mt  ngha biu t khc nhau, v vy bn hy cng Hoayeuthuong.com tm hiu xem b hoa mnh tng mang thng ip g nh:</p>
							<p>Hoa hng : tng trng cho mt tnh yu nng chy v lng mn. Hoa hng   c t rt lu i, tri qua nhiu nn vn ha - c phng ty v phng ng.Nhng  bt k u, n cng lun l loi hoa c yu thch nht, v biu tng cho mt th tnh cm v cng thing ling: Tnh yu.</p>
							<p>Hoa hng trng: tng trng cho s ngy th, duyn dng v s cm thng. Hng trng khng ch biu tng cho mt tnh yu thun khit, cao thng m mt khc, n cn l loi hoa dnh  by t li xin li n ngi yu nu chng may bn c mc li lm.</p>
							<p>Hoa hng vng: C l y l loi hoa lm ngi nhn kh au u bi  ngha kh a dng ca n: Hoa hng vng khng ch tng trng cho mt tnh yu kiu sa y rc r, m n cn l loi hoa ca tnh bn, th hin s quan tm v tin trin trong mi quan h, chng hn nh: "anh quan tm n em" hoc "hy nh n anh". Tng mt b hng vng c th l mng ngy khi u, v c mng ngy tr v. i khi,hng vng li l mt li by t s ghen tung, tnh yu gim st, v s phn bi.
																	 Bn cnh , hoa hng vng cng l loi hoa l tng  dnh tng m v b, bi l n cn c  ngha ca s bit n v tn knh.</p>
						</div>
					</div>
				</div>
			</>
		);
	}
}

export default BlogDetail;